<template>
<div>
  <div class="restaurant-container">
    <div class="restaurant"
         v-for="restaurant in restaurants"
         v-bind:key="restaurant._id"
    >
      <span>
      {{restaurant.name}}
      </span>

    </div>
    <GoogleMapLoader
            :allAddresses="allAddresses"
            :allCoords="allCoords"
    >

    </GoogleMapLoader>
  </div>
</div>
</template>

<script>
    import RestaurantService from "../RestaurantService";
    import GoogleMapLoader from './GoogleMapLoader.vue'
    export default {
        name: "Results",
        components:{
            GoogleMapLoader,
        },
        data() {
            return {
                address: '',
                allAddresses: [],
                allCoords: [],
            }
        },
        computed: {
            mapConfig () {
                return {
                    center: { lat: 0, lng: 0 }
                }
            },
        },
        async created() {
            try {
                this.restaurants = await RestaurantService.getRestaurants();
                this.address = this.restaurants[0].address
                this.restaurants.forEach(restaurant => {
                    this.allAddresses.push(restaurant.address);
                    this.allCoords.push(restaurant.position);
                });
                this.createMap()

            } catch (err) {
                this.error = err.message;
            }
        },
    }
</script>

